.curriculo-container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.curriculo-section {
  margin-bottom: 40px;
}

.curriculo-section h2 {
  font-size: 1.8em;
  margin-bottom: 20px;
  color: #333;
}

.curriculo-card-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  gap: 20px;
}

.curriculo-card {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
