.curriculo-card h3 {
  margin-top: 0;
  font-size: 1.3em;
  color: #444;
}

.curriculo-card p {
  margin: 5px 0;
  font-size: 0.9em;
  color: #666;
}

@media (max-width: 768px) {
  .curriculo-card {
    -ms-flex: 1 1 100%;
        flex: 1 1 100%;
    max-width: none;
  }
}



